export default {
    state: {
        status: "matching",  //matchingplaying
        socket: null,  // WebSocket
        opponent_username: "",  //
        opponent_photo: "",  //

        gameMap: "null",  //
        idA: 0,
        sxA: 0,
        syA: 0,
        idB: 0,
        sxB: 0,
        syB: 0,

        gameObject: null,
        loser: "none",  //none, all, A, B  
        time: "",  //start stop success 
    },
    getters: {
    },
    mutations: {  // 
        updateSocket(state, socket) {  
            state.socket = socket
        },

        updateOpponent(state, opponent) {  //2data,,
            state.opponent_username = opponent.opponent_username
            state.opponent_photo = opponent.opponent_photo
        },

        updateStatus(state, status) {
            state.status = status
        },

        updateGame(state, game) {
            state.gameMap = game.map
            state.idA = game.idA
            state.sxA = game.sxA
            state.syA = game.syA
            state.idB = game.idB
            state.sxB = game.sxB
            state.syB = game.syB
        },
        updateGameObject(state, gameObject) {
            state.gameObject = gameObject
        },
        updateLoser(state, loser) {
            state.loser = loser
        },
        updateTime(state, time) {
            state.time = time
        }
    },
    actions: {  //

    },
    modules: {
    }
}
