const AC_GAME_OBJECT = [];  //

export class AcGameObject {
    constructor() {  //
        AC_GAME_OBJECT.push(this);  //
        this.has_called_start = false;  // start 
        this.timedelta = 0;  //
    }

    start() {  //

    }

    update() {  //

    }

    on_destroy() {  //

    }

    destroy() {
        this.on_destroy();  // on_destroy 

        for(let i in AC_GAME_OBJECT) {
            const obj = AC_GAME_OBJECT[i];
            if(obj === this) {
                AC_GAME_OBJECT.splice(i, 1);  // i  1 
            }
        }
    }
}

let last_timedelta;  //
const step = timedelta => {  //step
    for(let obj of AC_GAME_OBJECT) {
        if (!obj.has_called_start) {
            obj.has_called_start = true;
            obj.start();
        } else {
            obj.timedelta = timedelta - last_timedelta;
            obj.update();
        }
    }
    last_timedelta = timedelta;
    requestAnimationFrame(step);  //
}

requestAnimationFrame(step);
